@font-face {
    font-family: "Akira";
    src: url("../../../fonts/Akira.otf") format("opentype");
}

.loading-screen.hidden {
    opacity: 0; /*    */
    pointer-events: none; /*      */
}

.warning-message {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    background-color: red;
    color: white;
    padding: 10px;
    border-radius: 0;
}

.loading-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black; /*   */
    opacity: 1; /*   */
    transition: opacity 0.5s ease-in-out; /*   */
    z-index: 9;
}

.loading-screen h1 {
    font-size: 5vw;
    font-family: Akira;
    font-weight: bold; /*   */
    color: white; /*   */
    margin: 5px; /*  */
    opacity: 0; /*   */
    animation: fadeIn 0.6s ease-in-out forwards; /*   */
}

.loading-screen h1:nth-child(1) {
    animation-delay: 0.1s; /*     */
}

.loading-screen h1:nth-child(2) {
    animation-delay: 0.4s; /*     */
}

.loading-screen h1:nth-child(3) {
    animation-delay: 0.6s; /*     */
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }

}

